@media (max-width:1160px) {
    .loginpage {
        grid-template-columns: 1fr;
    }

    .login_img {
        display: none;
    }

    .see-pass {
        width: 90vw;
    }

    .allPassDiv {
        height: 400px;
        width: 85vw;
    }

    .generate-pass {
        height: 600px;
        width: 90vw;
    }

    #generated_pass_div {
        width: 80%;
    }

    .customise_pass_div {
        width: 80%;
    }

    .generate_pass_nav_links {
        width: 80%;
    }
}

@media (max-width:1024px) {
    .login_img img {
        height: 250px;
    }

    .login_div form {
        height: 500px;
        width: 70vw;
    }

    .signin_p {
        text-align: center;
    }

    .signin_line {
        text-align: center;
    }
}

@media (max-width:980px) {
    .user h1 {
        font-size: 1.8rem;
    }

    .logout {
        width: 120px;
        height: 35px;
        font-size: 0.9rem;
    }

    .nav_btn a {
        font-size: 1rem;
        height: 30px;
        width: fit-content;
        padding: 0 10px;
    }
}

@media (max-width:840px) {
    .generate-pass {
        width: 100vw;
        box-shadow: none;
    }
}

@media (max-width:790px) {
    .user h1 {
        font-size: 1.8rem;
        justify-content: start;
        padding: 0 20px;
    }

    .save-pass input {
        width: 70vw;
    }

    .save-pass {
        width: 100vw;
    }

    #save {
        height: 35px;
        width: 150px;
        font-size: 1rem;
    }

    .logoutIcon {
        display: block;
        font-size: 2rem;
        color: #d63031;
        cursor: pointer;
    }

    .logout {
        display: none;
    }

    .nav_btn {
        height: 150px;
        flex-direction: column;
    }

    .nav_btn a {
        width: 290px;
        height: 40px;
    }
}

@media (max-width:750px) {
    .generate-pass h1 {
        font-size: 1.5rem;
    }

    #generated_pass_div {
        width: 90%;
    }

    .customise_pass_div {
        width: 90%;
    }

    .generate_pass_nav_links {
        width: 90%;
    }
}

@media (max-width:590px) {
    .generate-pass button {
        width: 110px;
        height: 35px;
        font-size: 0.9rem;
    }

    .generate-pass h1 {
        font-size: 1.2rem;
    }

    .customise_pass_div {
        height: 250px;
    }

    .generate-pass h2 {
        font-size: 1.2rem;
    }
}

@media (max-width:450px) {
    .login_img img {
        height: 200px;
    }

    .signin_p {
        justify-content: center;
    }

    .login_div form {
        height: 50vh;
        width: 95vw;
    }

    .user {
        height: 60px;
    }

    .user h1 {
        font-size: 1.5rem;
    }

    .logoutIcon {
        font-size: 1.5rem;
    }

    .save-pass {
        height: 600px;
    }

    .save-pass h3 {
        font-size: 1.6rem;
        /* border: 1px solid rgba(0, 0, 0, 0.289); */
        width: 90vw;
        text-align: center;
    }

    .see-pass {
        width: 100vw;
        height: 85vh;
    }

    .generate-pass {
        width: 100vw;
    }

    .allPassDiv {
        width: 95vw;
        /* height: 50vh; */
        /* border: 1px solid #adccfa; */

    }

    .details {
        font-size: 1.3rem;
    }

    .passList {
        margin: 10px 0;
        /* border: 1px solid #adccfa; */
        /* background-color: #EAF2FE; */
        box-shadow: 3px 3px 16px 0 #CED6E5,
            -8px -8px 12px 0 #ffffff;
    }

    .passList p {
        font-size: 0.9rem;
    }

    .generate-pass {
        height: 700px;
    }

    .generate-pass h1 {
        font-size: 1.4rem;
        text-align: center;
    }

    .generate_pass_nav_links {
        flex-direction: column;
        height: 150px;
    }

    .generate_pass_nav_links a {
        height: 40px;
        width: 200px;
    }
}

@media (max-width:375px) {
    .login_img img {
        height: 170px;
    }

    .login_div form {
        height: 45vh;
        width: 95vw;
    }

    .signin_p {
        font-size: 1.5rem;
    }

    .signin_line {
        font-size: 0.9rem;
    }

    .login_div button {
        font-size: 1rem;
    }

    .save-pass {
        height: 500px;
    }

    .generate-pass {
        height: 600px;
    }

    .generate-pass h1 {
        font-size: 1.2rem;
        color: transparent;
    }

    .details {
        font-size: 1.1rem;
    }
}