@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@1,500&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    height: 100vh;
    width: 100vw;
}

body button:focus {
    background-color: transparent;
}

body button:active {
    background-color: transparent;
}

.App {
    height: 100vh;
    width: 100vw;
    /* background-image: url('./images/bgn2.webp'); */
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.loginpage {
    height: 100vh;
    width: 100vw;
    /* background-color: rgba(0, 0, 0, 0.441); */
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.login_div {
    /* border: 1px dotted white; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.login_div form {
    position: relative;
    /* border: 1px solid black; */
    /* background-color: rgba(92, 92, 92, 0.277); */
    height: 450px;
    width: 450px;
    border-radius: 20px;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.authLink {
    text-decoration: none;
    color: #d63031;
}

.signin_p {
    color: black;
    font-size: 2rem;
    width: 100%;
    display: flex;
    align-items: center;
    /* border: 1px solid black; */
}

.loginIcon {
    color: #3D43CD;
    margin-left: 10px;
    font-size: 1.5rem;
}

.signin_line {
    color: #3D43CD;
    width: 100%;
}

.login_div input {
    display: block;
    background-color: transparent;
    border: 1px solid #4b4fc1;
    height: 40px;
    width: 100%;
    color: black;
    border-radius: 5px;
    padding-left: 10px;
}

.login_div input:focus {
    outline: none;
    border: 2px solid #3D43CD;

}

.login_div button {
    height: 40px;
    width: 100%;
    background-color: #3D43CD;
    border: none;
    border-radius: 5px;
    color: aliceblue;
    cursor: pointer;
}

.login_img {
    /* border: 1px dotted white; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    position: relative;
    background-color: #3D43CD;
}

.login_img img {
    /* border: 1px dotted white; */
    height: 40%;
}

nav {
    position: absolute;
    top: 50px;
    display: flex;
    align-items: center;
}

nav .navIcon {
    /* height: 45px;width: 45px; */
    border-radius: 50%;
    color: #d63031;
    font-size: 1.5rem;
}

nav h1 {
    color: #FF6363;
    color: #148e76;
    color: #3D43CD;
    font-size: 2rem;
    font-family: 'Playfair Display', serif;

}


/* homepage */

.homepage {
    height: 100vh;
    width: 100vw;
    /* color: aliceblue; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    position: relative;
    /* background-color: #3D43CD; */
    /* background-image: url('./images/bgn2.webp');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover; */
    background-color: white;
    background-color: #EBEEF7;
}

.user {
    /* border: 1px dotted white; */
    height: 8vh;
    width: 100vw;
    display: grid;
    grid-template-columns: 5fr 1fr;
    /* padding: 5px 5vw; */
    position: absolute;
    top: 0;
    /* background-color: rgba(0, 0, 0, 0.214); */
    background-color: #3d42cd;
    background-color: #EBEEF7;
    box-shadow: 3px 3px 16px 0 #CED6E5,
        -8px -8px 12px 0 #ffffff;
}

.user h1 {
    /* border: 1px dotted white; */
    display: flex;
    align-items: center;
    justify-content: center;
    color: #4b4fc1;
}

.user_btn_div {
    display: flex;
    align-items: center;
    justify-content: center;
}


.logout {
    display: block;
    width: 150px;
    height: 40px;
    background-color: #EBEEF7;
    color: #d63031;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
    box-shadow: 3px 3px 16px 0 #CED6E5,
        -8px -8px 12px 0 #ffffff;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
}

.logoutIcon {
    display: none;
}

.save-pass {
    /* background-color: rgba(255, 255, 255, 0.136); */
    /* background-color: #3d42cd75; */
    /* border: 1px solid rgba(0, 0, 0, 0.289); */
    height: 70vh;
    width: 70vw;
    border-radius: 5px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: #EBEEF7;
    box-shadow: 3px 3px 16px 0 #CED6E5,
        -8px -8px 12px 0 #ffffff;
}

.save-pass h3 {
    font-size: 1.5rem;
    color: #8291A4;
}

.save-pass span {
    /* border: 1px solid rgba(0, 0, 0, 0.289); */
    width: fit-content;
    /* padding: 10px; */
}

.save-pass label {
    color: #9E8FD4;
    font-size: 1.1rem;
    display: block;
}

.save-pass input {
    display: block;
    color: #000000;
    font-size: 1.1rem;
    border: none;
    border-bottom: 1px solid #555557;
    width: 550px;
    margin: 5px 0;
    background-color: transparent;
    padding: 5px;
}

.save-pass input:focus {
    /* width: 550px; */
    outline: none;
}

#save {
    height: 40px;
    width: 200px;
    cursor: pointer;
    background-color: #EBEEF7;
    border: none;
    border-radius: 5px;
    font-size: 1.1rem;
    color: #d63031;
    box-shadow: 3px 3px 16px 0 #CED6E5,
        -8px -8px 12px 0 #ffffff;
}

.nav_btn {
    width: 100%;
    /* border: 1px solid rgba(0, 0, 0, 0.289); */
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.nav_btn a {
    font-size: 1.1rem;
    color: #3d42cd;
    text-decoration: none;
    /* border: 2px solid #4F7293; */
    display: block;
    height: 35px;
    width: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 3px;
    background-color: #EBEEF7;
    box-shadow: 3px 3px 12px 0 #CED6E5,
        -8px -8px 16px 0 #ffffff;
}





.see-pass {
    height: 70vh;
    width: 70vw;
    border-radius: 20px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.see-pass p {
    font-weight: 900;
}

.details {
    font-size: 1.9rem;
    color: #3d42cd;
}

.allPassDiv {
    height: 400px;
    width: 60vw;
    border-radius: 5px;
    background-color: #EBEEF7;
    box-shadow: 3px 3px 12px 0 #CED6E5 inset,
        -8px -8px 12px 0 #ffffff inset;
    white-space: nowrap;
    overflow-x: hidden;
    overflow-y: scroll;
    padding: 10px;
    margin: 50px 0;
}

.passList {
    list-style: none;
    width: 100%;
    padding: 4px 4px;
    border-radius: 5px;
    cursor: pointer;
    border: 1px solid transparent;
}

.passList:hover {
    box-shadow: 3px 3px 16px 0 #CED6E5,
        -8px -8px 12px 0 #ffffff;
}

.passList p {
    color: #d63031;
    width: 95%;
    margin: auto;
    margin-top: 5px;
    text-align: center;

}

.passShow {
    height: 35px;
    overflow: hidden;
    overflow-x: scroll;
}

.passList p span {

    color: #555557;
    margin-right: 10px;
}

.more {
    font-size: 1.5rem;
    margin-top: 10px;
    cursor: pointer;
}

.save-pass-btn {
    height: 40px;
    width: 150px;
    background-color: #EBEEF7;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: #000000;
    border-radius: 3px;
    box-shadow: 3px 3px 16px 0 #CED6E5,
        -8px -8px 12px 0 #ffffff;
    margin: 10px 0;
}


/**/

.generate-pass {
    height: 650px;
    width: 70vw;
    border-radius: 5px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    box-shadow: 3px 3px 16px 0 #CED6E5,
        -8px -8px 12px 0 #ffffff;
}

.generate-pass h1 {
    color: #8291A4;
}

#generated_pass_div {
    height: 50px;
    width: 70%;
    display: flex;
    align-items: center;
    padding: 0 20px;
    white-space: nowrap;
    overflow: hidden;
    overflow-x: scroll;
    border-radius: 3px;
    box-shadow: 3px 3px 12px 0 #CED6E5 inset,
        -8px -8px 12px 0 #ffffff inset;
    color: #9E8FD4;
    color: #d63031;
}

.generate-pass button {
    background-color: transparent;
    border: none;
    height: 40px;
    width: 200px;
    margin: 5px;
    box-shadow: 3px 3px 16px 0 #CED6E5,
        -8px -8px 12px 0 #ffffff;
    cursor: pointer;
    border-radius: 3px;
    color: #d63031;
    font-size: 1rem;
}

.generate-pass h2 {
    color: #9E8FD4;
}

.customise_pass_div {
    /* border: 1px solid #000000; */
    height: 200px;
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    box-shadow: 3px 3px 16px 0 #CED6E5,
        -8px -8px 12px 0 #ffffff;
}

.customise_pass_div span {
    width: 200px;
    /* border: 1px solid #000000; */
    color: #8291A4;
}

.customise_pass_div span input {
    width: 40px;
    cursor: pointer;
    margin-left: 5px;
    border: none;
    border-bottom: 0.1px solid #9E8FD4;
    background-color: transparent;
    text-align: center;
    color: #9E8FD4;
}

.customise_pass_div span input:focus {
    outline: none;
}

.customise_pass_div span label {
    font-size: 1rem;
    cursor: pointer;
}

.generate_pass_nav_links {
    /* border: 1px solid black; */
    width: 70%;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.generate_pass_nav_links a {
    /* display: block; */
    height: 35px;
    width: 200px;
    margin: 10px 5px;
    box-shadow: 3px 3px 16px 0 #CED6E5,
        -8px -8px 12px 0 #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #3d42cd;
    text-decoration: none;
}